#head{
background:red;
color:white;
height:70px;
display:flex;
justify-content: space-around;
}
.flexcenter{
  align-items: center;
  display:flex;
 width:100%;
  height:100%;
  text-align:center;
}
#video{
  display:grid;
  grid-template-columns: auto auto auto;
  
}

@media (max-width:900px) {
  #video{grid-template-columns: auto auto}  
  }
  @media (max-width:600px) {
    #video{grid-template-columns: auto}  
    }
iframe{
  margin-right: auto;
  margin-left: auto;
  width:90%
  
}

